import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import {Fab,Tooltip,Modal, Container, MenuItem,FormLabel,RadioGroup,FormControlLabel,Radio, Button,Snackbar} from "@material-ui/core";
import MuiAlert from '@material-ui/lab/Alert';
import AddIcon from '@material-ui/icons/Add';
import TextField from '@material-ui/core/TextField';
import { useState } from "react";
const useStyles = makeStyles((theme)=>({
    fab:{
        position:"fixed",
        bottom:20,
        right:20,
    },
    container:{
        width:400,
        height:550,
        backgroundColor:"white",
        position:"absolute",
        top:0,
        bottom:0,
        left:0,
        right:0,
        margin:"auto",
        [theme.breakpoints.down("sm")]:{
            width:"100vw",
            height:"100vh",
        },
    },
    form:{
        padding:theme.spacing(2),

    },
    item:{
        marginBottom:theme.spacing(3),
    },

}));
function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
  }
function Add() {
const classes=useStyles();
const [open,setOpen]=useState(false);
const [openAlert,setOpenAlert]=useState(false);
const handleClick = () => {
    setOpenAlert(true);
  };

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpenAlert(false);
  };
  return (
    <>

    <Tooltip title="Add" aria-label="add" onClick={()=>setOpen(true)}>
    <Fab color="primary" className={classes.fab}>
      <AddIcon />
    </Fab>
  </Tooltip>
  <Modal open={open}>
      <Container className={classes.container}>
      <form className={classes.form} autoComplete="off">
          <div className={classes.item}>
          <TextField id="standard-basic" label="Title" size="small" style={{width:"100%"}}/>
          </div>
          <div className={classes.item}>
          <TextField id="outlined-multiline-static"
          label="Description"
          multiline
          rows={4}
          defaultValue="Tell Your Story...."
          variant="outlined"
          size="small" 
          style={{width:"100%"}}

          />
          </div>
          <div className={classes.item}>
          <TextField select label="Visbility" value="Public">
          <MenuItem value="Public">Public</MenuItem>
          <MenuItem value="Public">Private</MenuItem>
          <MenuItem value="Public">Unlisted</MenuItem>
          </TextField>
          </div>
          <div className={classes.item}>
          <FormLabel component="legend">Who can Comments?</FormLabel>
          <RadioGroup>
          <FormControlLabel value="Everybody" control={<Radio size="small"/>} label="Everybody" />
          <FormControlLabel value="My Friends" control={<Radio size="small" />} label="My Friends" />
          <FormControlLabel value="Nobody" control={<Radio size="small"/>} label="Nobody" />
          <FormControlLabel value="Custom" disabled control={<Radio size="small"/>} label="Custom(Premuim)" />
          </RadioGroup>
          </div>
          <div className={classes.item}>
              <Button variant="outlined" color="primary" style={{marginRight:20}} onClick={()=>setOpenAlert(true)}>Create</Button>
              <Button variant="outlined" color="secondary" onClick={()=>setOpen(false)}>Cancel</Button>
          </div>
      </form>
      </Container>
  </Modal>
  <Snackbar open={openAlert} autoHideDuration={4000} onClose={handleClose} anchorOrigin={{ vertical: 'bottom', horizontal: 'left' }}>
        <Alert onClose={handleClose} severity="success">
          This is a success message!
        </Alert>
  </Snackbar>
  </>
  );
}
export default Add;